import React, { memo, useMemo } from 'react';
import { TypeActionInteraction } from 'containers/Home/types';
import { DefaultIconWrapper } from './styled';

interface Props {
  bgColorDefault: string;
  iconColorDefault: string;
  typeHotspot: TypeActionInteraction;
}

const DefaultIcon = ({
  typeHotspot,
  bgColorDefault,
  iconColorDefault,
}: Props) => {
  const classIcon = useMemo(() => {
    switch (typeHotspot) {
      case 'point':
        return 'map-marker-alt';
      case 'article':
        return 'newspaper';
      case 'link':
        return 'link';
      case 'sound':
        return 'waveform';
      case 'video':
        return 'video';
      case 'image':
        return 'images';
      case 'product':
        return 'tshirt';
      default:
        return '';
    }
  }, [typeHotspot]);

  return (
    <DefaultIconWrapper bgColor={bgColorDefault} iconColor={iconColorDefault}>
      <i aria-hidden className={`fas fa-${classIcon}`} />
    </DefaultIconWrapper>
  );
};

export default memo(DefaultIcon);
